:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --Lime-green: hsl(163, 72%, 41%);
  --Bright-Red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear gradient hsl(37, 97%, 70%) to hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--dark-text2);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}
.toggle {
  border: none;
}
.toggle label[for=Dark] {
  color: var(--dark-text1);
  font-weight: 700;
  margin: 0 0.5rem 0;
  cursor: pointer;
}
.toggle__wrapper {
  font-size: 12px;
  display: inline-block;
  vertical-align: middle;
  width: 48px;
  height: 1.5rem;
  border-radius: 1.5rem;
  /* = height */
  position: relative;
}
.toggle input[type=radio] {
  display: inline-block;
  margin-right: -2px;
  width: 1rem;
  height: 100%;
  opacity: 0;
  position: relative;
  z-index: 1;
  cursor: pointer;
}
.toggle input[type=radio]:checked {
  background: radial-gradient(teal 0%, teal 40%, transparent 50%, transparent);
}
.toggle__background {
  display: block;
  height: 100%;
  border-radius: 0.75rem;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 0;
  transition: all 0.1s ease-out;
}
.toggle__button {
  background-color: var(--dark-top-bg);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 100%;
  width: 50%;
  height: 100%;
  border: 1px solid var(--toggle);
  border-radius: 50%;
  transition: all 0.1s ease-out;
  z-index: 2;
}
.toggle #dark:checked ~ .toggle__button {
  right: 0;
  left: 50%;
}

.card {
  padding: 1.25rem;
}